//搜索
mock.onGet('/api/search').reply(function(req) {
	var count = 0;
	var list = shopJson.shop;
	var res = [];
	console.log(req.params.search.split(""))
	var search = req.params.search.split("");
	for (var i = 0; i < list.length; i++) {
		for (var j = 0; j < search.length; j++) {
			if (list[i].name.indexOf(search[j]) != -1) {
				count ++;
				if (count >= 2) {
					res.push({
						pic:shopJson.shop[i].mealsrc,money:shopJson.shop[i].price,name:shopJson.shop[i].mealname,shop:shopJson.shop[i].name
					});
					break;
				}
			}
		}
	}
	return [200,{search: res}]
})

//详情
