import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  // 定义状态
  state: {
  	discoverDetail_show: false,
  	discoverClose_show:false,
  	myPage_show: true,
   	success:false,
   	discover:true,
   	del:{},
   	judge:false,
   	judgeMore:false,
   	judgeHome:false,
   	center: {
   		lng: 113.271931,
		lat: 23.147308
   	},
   	disP: false,
   	collect:false,//控制隐藏消息积分界面
   	foot:false,//控制我的足迹显示隐藏
   	post:false,//控制我的优惠显示隐藏
   	load:false,//控制我的上传显示隐藏
   	reg:false,//控制注册的显示隐藏
   	user:{},//用户信息接收传递
   	loadimg:false//上传图片
  }
})

export default store


